let tags = [
  "skin",
  "skin care",
  "skin type",
  "environment",
  "code",
  "visual studio code",
  "javascript",
  "vs code",
]
class Search extends React.Component {
  tempArr = new Set()
  key = 0
  constructor(props) {
    super(props)
    this.state = {
      showAutoComplete: "",
      autoCompleteArray: [],
    }
    console.log(props)
  }
  handelInputChange = event => {
    this.setState({ autoCompleteArray: [] })
    if (event.target.value.length > 0) {
      tags.forEach((tag, key) => {
        if (tag.search(event.target.value) > -1) {
          this.tempArr.add(tag)
        } else {
          this.tempArr.delete(tag)
        }
      })
      this.setState({
        autoCompleteArray: Array.from(this.tempArr),
        showAutoComplete: "show",
      })
    }
  }
  handelSubmit = event => {
    event.preventDefault()
    console.log("This is where i require states")
  }
  handelKeyDown = event => {
    //console.log("Key down function ", event.keyCode)
    if (event.keyCode == 9) {
      console.log("Key down function ", event.keyCode)
      event.preventDefault()
    }
  }
  handelOnFocusTextInput = event => {
    console.log("on blur function call")

    if (this.state.showAutoComplete === "show")
      this.setState({ showAutoComplete: "" })
    else if (this.state.autoCompleteArray.length != 0)
      this.setState({ showAutoComplete: "show" })
  }

  render() {
    return (
      <div class="col">
        <form class="form-inline my-2 my-lg-0 row" onSubmit={this.handelSubmit}>
          <input
            class="form-control mr-sm-2"
            type="search"
            placeholder="Search"
            aria-label="Search"
            onChange={this.handelInputChange}
            onKeyDown={this.handelKeyDown}
            onFocus={this.handelOnFocusTextInput}
            onBlur={this.handelOnFocusTextInput}
          ></input>

          <button class="btn btn-outline-success my-2 my-sm-0">Search</button>
        </form>
        {this.state.autoCompleteArray.length != 0 ? (
          <span className={"search-span  " + this.state.showAutoComplete}>
            <div
              class="mr-sm-2 search-result-box"
              style={{ listStyle: "none" }}
            >
              {this.state.autoCompleteArray.map((tag, key) => {
                return <p class="search-result-item">{tag}</p>
              })}
            </div>
          </span>
        ) : (
          <></>
        )}
      </div>
    )
  }
}
export default Search
